.display-loding {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
}
.gutters-0{
    padding: 0;
    margin: 0;
}
.user-card {
    height: 200px;
    border-radius: 25px;
}

.profile-pic-container {
    height: 90%;
}

.profile-pic {
    height: 100%;
    width: auto;
    border: solid #cbcbcb 1px;
}

.info-summary-container {
    height: 100%;
    padding: 10px;
}

.info-summary {
    position: relative;
    height: 100%;
}

.bio-info {
    margin-top: 20px;
}

.bottom-link {
    position: absolute;
    bottom: 0;
    width: 100%;
    margin-bottom: 10px;
}

.bottom-link > span {
    margin-left: -5px;
    cursor: pointer;
    font-weight: bold;
    padding: 10px;
    border-radius: 30px;
    align-items: center;
    text-align: center;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.bottom-link > span:hover {
    background-color: #1e9bff;
}

.badge {
    margin-left: 5px;
}

.badge-orange {
    background-color: darkorange;
}
.repo-wraper {
    background-color: #ffffff;
    border-radius: 5px;
    border-style: solid;
    border-width: thin;
    padding: 20px 10px 20px 0;
    margin: 10px 0px;
    cursor: pointer;
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

.repo-wraper:hover {
    border-color: darkorange;
    background-color: #c7c7c7;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.language-indicator {
    margin-right: 5px;
    padding: 5px;
    background-color: purple;
}

.language {
    margin-right: 10px;
}

.badge-info>div {
    margin-bottom: 15px;

}

.date {
    font-weight: bold;
}

.chip {
    display: inline-block;
    padding: 0 25px;
    height: 50px;
    font-size: 16px;
    line-height: 50px;
    border-radius: 25px;
    background-color: #24e7ff;
}

.chip img {
    float: left;
    margin: 0 10px 0 -25px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
}
